import styled from "styled-components";

export const FriendList = styled.div`
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: flex-start;
  margin: 10px;
`;

export const Friend = styled.div`
  border: 1px solid black;
  border-radius: 5px;
  display: grid;
  grid-template-columns: 30% 70%;
  align-items: center;
  justify-content: center;
  margin: 10px;
  width: auto;
  min-height: 25%;
  max-height: 30%;

  img {
    height: 100%;
    width: auto;
  }
`;

export const FriendInfo = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;

  a {
    text-decoration: none;
    align-items: center;
    justify-content: space-evenly;
  }

  h2 {
    font-family: "Raleway", sans-serif;
    font-weight: bold;
    font-size: 24px;
    justify-self: center;
  }

  h3 {
    font-family: "Raleway", sans-serif;
    font-weight: normal;
    font-size: 12px;
    justify-self: center;
  }

  button {
  }
`;
