import React, { Component } from "react";
import { rdfService, ldflexService, fileExplorerService } from "@services";
import { ThemeProvider } from "styled-components";
import { Explorer, ExplorerItem, Route } from "./fileexplorer.style";

export default class FileExplorerComponent extends Component {
  constructor() {
    super();
    this.state = {
      url: "",
      folder: [],
      route: [],
      previousRoute: []
    };
    this.changeRoot = this.changeRoot.bind(this);
  }

  async componentDidMount() {
    let publicRoot = await fileExplorerService.getRoot();
    console.log(publicRoot);
    await this.setState({
      url: publicRoot,
      folder: await fileExplorerService.getFolderContent(publicRoot),
      route: await this.getRoute(publicRoot)
    });
    console.log(this.state);
  }

  async changeRoot(newRoute) {
    let prev = this.state.route;
    await this.setState({
      url: newRoute,
      folder: await ldflexService.getFolderContent(newRoute),
      route: await this.getRoute(newRoute),
      previousRoute: prev
    });
  }

  async getRoute(url) {
    let route = url.split("//");
    route = route[1].split("/");
    let newRoute = [];
    newRoute.push({
      name: "home",
      url: await fileExplorerService.getRoot()
    });
    for (var i = 1; i < route.length; i++) {
      if (route[i] !== "") {
        let part = {};
        part.name = route[i];
        let url = "https://" + route[0] + "/";
        for (var j = 1; j <= i; j++) {
          url += route[j] + "/";
        }
        part.url = url;
        newRoute.push(part);
      }
    }
    return newRoute;
  }

  /* jshint ignore:start */
  render() {
    console.log(this.state);
    let folders = null;

    if (this.state.folder.folders !== undefined) {
      folders = this.state.folder.folders.map(file => {
        return (
          <ExplorerItem key={file.url}>
            <span onClick={() => this.changeRoot(file.url)}>{file.name}</span>
            <span>{file.modified}</span>
            <span>{file.size}</span>
          </ExplorerItem>
        );
      });
    }

    let archives = null;

    if (this.state.folder.files !== undefined) {
      archives = this.state.folder.files.map(file => {
        return (
          <ExplorerItem key={file.url}>
            <span>{file.name}</span>
            <span>{file.modified}</span>
            <span>{file.size}</span>
          </ExplorerItem>
        );
      });
    }

    const route = (
      <Route>
        {this.state.route.map(part => {
          return (
            <span onClick={() => this.changeRoot(part.url)}>{part.name}/</span>
          );
        })}
      </Route>
    );

    return (
      <Explorer>
        {route}
        {folders}
        {archives}
      </Explorer>
    );
  }
  /* jshint ignore:end */
}
